import React, {Component} from 'react';
import { Card, CardImg, Modal, ModalHeader, ModalBody,  } from 'reactstrap';
import { InfoCircle } from 'react-bootstrap-icons';
import gsap from 'gsap';

class Projects extends Component {

    constructor(props) {
        super(props);

        this.state = {
            selectedProject:null,
            isModalOpen: false,
        }

        this.toggleModal = this.toggleModal.bind(this);
    }

    onProjectSelect(project) {
        this.setState({ selectedProject: project});
    }

    toggleModal(){
        this.setState({
            isModalOpen: !this.state.isModalOpen
        });
    }

    handleFocus(){
        setTimeout(()=>{
            const tl = gsap.timeline();
            tl.to(".circle", {duration: 1, rotation:360});
        }, 1000)
    }

    renderProject(project){
       if(project != null){ 
            return(
                <Modal size="lg" isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                    <ModalHeader toggle={this.toggleModal} cssModule={{'modal-title': 'w-100 text-center'}}>
                        {project.title}    
                    </ModalHeader>
                    <ModalBody>
                        <div className="cointainer">
                            <div className="row justify-content-center col-xs-12">
                                <img className="img-fluid" src={project.image} alt={project.title} width="400" height="350"/>
                            </div>
                            <div className="row justify-content-center mt-5">
                                <a href={project.moreInformation}> <InfoCircle onFocus={this.handleFocus()} className="circle" size={54}/></a>
                            </div>
                            <div className="row m-5">
                                {project.description}
                            </div>
                        </div>
                    </ModalBody>
                </Modal>
            );
       }else{
           return(
               <div></div>
           );
       }
    }

    wrapperFunction(project){
        this.onProjectSelect(project);
        this.toggleModal();
    }

    render(){

        const projects = this.props.projects.map( (project) => {
            return(
                <div className="col-sm-4 col-xs-12 image-container mb-2">
                    <Card key={project.id} onClick={() => this.wrapperFunction(project)} >
                        <CardImg width="100%" object src={project.image} alt={project.title} />
                    </Card>
                </div>
            );
        });

        return (
            <div className="container">
                <div className="row">
                    <div className="col-12">
                        <h3 id="portfolio" className="text-center">Portfolio</h3>
                        <hr />
                    </div>
                </div>
                <div className="row">
                    {projects}
                </div>
                <div className="row">
                  <div  className="col-12 col-md-5 m-1">
                    {this.renderProject(this.state.selectedProject)}
                  </div>
                </div>
            </div>
        );
    }

}

export default Projects;